import React, { useEffect, useState } from 'react'
import {useParams} from 'react-router-dom'
import tmdbApi from '../../api/tmdb'
import apiConfig from '../../api/apiConfig'
import './Details.scss'
import CastList from '../../components/CastList/CastList'
import VideoList from './VideoList'
import MoviesList from '../../components/Movie-list/Movies'
function Details() {
  const {category,id} =useParams()
  console.log(category,id)
  const [item,setItem] =useState(null)
  useEffect(()=>{
      const getDetail =async ()=>{
        const response = await tmdbApi.detail(category,id,{params:{}})
        setItem(response)
        window.scrollTo(0,0)
      }
      getDetail()
  },[category,id])
  return (
    <div>
        {item && (
          <>
          <div className="banner" style={{backgroundImage:`url(${apiConfig.originalImage(item.backdrop_path || item.poster_path)})`}}></div>
          <div className="mb-3 movie-content container">
              <div className="movie-content__poster">
                <div className="movie-content__poster__img" style={{backgroundImage:`url(${apiConfig.originalImage(item.backdrop_path || item.poster_path)})`}}>
                  
                </div>
              </div>
              <div className="movie-content__info">
                  <div className="title">
                    {
                      item.title ||item.name
                    }
                  </div>
                  <div className="genres">
                    {
                      item.genres && item.genres.slice(0,5).map((genre,i)=>(
                        <span key={i} className='genres__item'>{genre.name}</span>
                      ))
                    }
                    <p className='overview'>{item.overview}</p>
                    <div className="cast">
                      <div className="section__header">
                        <h2>Casts</h2>
                      </div>
                      <CastList id={item.id}/>
                    </div>
                    <VideoList id={item.id}/>
                  </div>
              </div>
          </div>
                  <div className="section mb-3">
                    <div className="section__header mb-2">
                        <h2>Similar</h2>
                    </div>
                    <MoviesList category={category} type='similar' id={item.id}></MoviesList>
                  </div>

          </>
        )}
    </div>
  )
}

export default Details
